const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)README\.storybook\.stories\.md)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(2);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)README\.storybook\.stories\.md)$/ */
      './' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)README\.[^/]*?\.stories\.md)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(2);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)README\.[^/]*?\.stories\.md)$/ */
      './' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/\.\.\/\.\.\/toolkit\/content\/widgets(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|mjs|ts|tsx|md))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(33);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/toolkit\/content\/widgets(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|mjs|ts|tsx|md))$/ */
      '../../../toolkit/content/widgets/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/aboutlogins\/content\/components(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mjs)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(34);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/aboutlogins\/content\/components(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mjs)$/ */
      '../aboutlogins/content/components/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/backup\/content(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mjs)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(18);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/backup\/content(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mjs)$/ */
      '../backup/content/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/\.\.\/\.\.\/toolkit\/components\/reader(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mjs)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(35);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/toolkit\/components\/reader(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.mjs)$/ */
      '../../../toolkit/components/reader/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.[\\/](?:stories(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|mjs|ts|tsx|md))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(10);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/stories(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|mjs|ts|tsx|md))$/ */
      './stories/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/\.\.\/\.\.\/toolkit\/themes\/shared\/design-system(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|mjs|ts|tsx|md))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(45);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/toolkit\/themes\/shared\/design-system(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|mjs|ts|tsx|md))$/ */
      '../../../toolkit/themes/shared/design-system/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}